.loader{
  height: 100vh;
  width:100%;
  display: flex;
  margin-top: 120px;
  justify-content: center;
  align-items: center;
}
.loader img { 
  width:128px;
  animation: spin 1s linear infinite;
}


@keyframes spin{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

