.abilities{
  margin-top:5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 100%;
  
}
.ability{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  margin: 0;
  position: relative;
}

.ability:nth-child(2),.ability:nth-child(3){
  border-top: 1px solid #ffffff22;
}
.ability p{
  margin: 5px 10px;
  padding: 0;
  font-size: .8rem;
  font-weight: bold;
  text-transform: capitalize;
  color:#00000000;
  white-space: nowrap;
  -webkit-background-clip: text;
          background-clip: text;
}

.ability span{
  color:#ccc;
  text-align: center;
  font-size: 0.8rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 5px;
}
.ability span label{
  cursor: pointer;
}
.ability span input{
  display: none;
}
.abilities input:checked ~ .tooltip{
  display:block;
}
.ability span .tooltip{
  content:attr(title);
  position: absolute;
  font-size: 1rem;
  line-height: 1rem;
  color: #ddd;
  background: #333;
  padding: 3px 6px;
  border-radius: 5px;
  top: 50%;
  left:50%;
  padding: 10px;
  -webkit-transform: translate(-50%,-60px);
          transform: translate(-50%,-60px);
  white-space: normal;
  text-align: center;
  width: 100%;
  z-index: 2;
  display: none;
}



.stats{
  font-size: .8rem;
