.pokedex{
  display: block;
  margin: 10px 0 10px 10px;
  width:30%;
  max-width: 160px;
}

.loading-container p{
  text-align: left;
  margin: 15px;
  color: white;
}

.loading-container p strong{
  text-transform: capitalize;
}
.loading-bar{
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  background: rgb(116, 201, 204);
}

.nav-container{
  box-sizing: border-box;
  margin-top: 5px ;
  width: 90%;
  max-width: 340px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
} 
input{
  height:35px;
  width: 80%;
  background: #2c2c2c;
  border: 1px solid #777;
  color: #ccc;
  border-radius: 5px;
  padding-left: 10px;
  font-size: 1.2rem;
  font-family: "PT Sans Narrow",sans-serif;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-shadow:inset 0 0 10px #00000099;
  
}
input:focus{
  background: #222;
  outline: none;
  border: 1px solid rgb(92, 174, 189);
}
input::-webkit-input-placeholder{
  text-align: center;
  font-size: 1rem;
}
input:-ms-input-placeholder{
  text-align: center;
  font-size: 1rem;
}
input::placeholder{
  text-align: center;
  font-size: 1rem;
}

select{
  height: 25px;
  margin-right: 10px;
  background: #333;
  border: 1px solid #777;
  color: #ccc;
  border-radius: 5px;
  padding:0 10px;
  font-size: 1rem;
  font-family: "PT Sans Narrow",sans-serif;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* box-shadow:inset 0 0 10px #00000099; */
}

select:focus{
  background: #333;
  outline: none;
  border: 1px solid rgb(92, 174, 189);
}

.loader{
  height: 100vh;
  width:100%;
  display: -webkit-flex;
  display: flex;
  margin-top: 120px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.loader img { 
  width:128px;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}


@-webkit-keyframes spin{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes spin{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


